<template>
	<view style=" height: 100vh;">
		<view class="subtitle">
			<view class="" style="text-align: center;">
				{{hitokoto}}
			</view>
			<view class="" style="text-align: center; margin-top: 20rpx;">
				----{{hitokotoFrom}}
			</view>
		</view>
		
		<view v-if="!islogin">
			<view style="text-align: center; font-size: 50rpx; margin-top: 300rpx;">请登录后再打卡</view>
		</view>
		
		<view v-if="islogin&&showadmin=='stu'" class="navigators">
			<view class = "navigatorBlock">
				<view style="background-image: url(../../static/clipboard-check.svg);" class="imageType">
					<navigator url="/pages/student/daka/daka" @tap="daka" class="navigatorType"></navigator>
				</view>
				<view style="line-height: 30rpx; text-align: center; font-style: Chewy; font-size: 40rpx;">打卡</view>
			</view>
			<view class="navigatorBlock" v-for="(item,index) in StudentNavigatorItems">
				<view class="imageType" :style="{ 'background-image': 'url(' + item.src + ')' }">
					<navigator :url='item.url' class="navigatorType"></navigator>
				</view>
				<view style="line-height: 30rpx; text-align: center; font-size: 40rpx;">{{item.text}}</view>
			</view>
		</view>
		
		<view v-if="islogin&&showadmin=='admin'">
			<view class = "navigators">
				<view class="navigatorBlock" v-for="(item,index) in administorNavigatorItems">
					<view class="imageType" :style="{ 'background-image': 'url(' + item.src + ')' }">
						<navigator :url='item.url' class="navigatorType"></navigator>
					</view>
					<view style="line-height: 30rpx; text-align: center; font-size: 40rpx;">{{item.text}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				StudentNavigatorItems: [{
						src:'../../static/dice-5.svg',
						text: "求签",
						url: '/pages/student/qiuqian/qiuqian'
					},
					{
						src: '../../static/people.svg',
						text: '查看打卡排名',
						url: '/pages/student/totalData/totalData'
					},
					{
						src: "../../static/person.svg",
						text: "查看个人数据",
						url: '/pages/student/selfData/selfData'
					},
				],
				administorNavigatorItems:[
					{
						src:"../../static/bar-chart.svg",
						text:'今日打卡数据',
						url:'/pages/administor/dayData'
					},
					{
						src:"../../static/graph-up.svg",
						text:'月度打卡数据',
						url:'/pages/administor/monthData'
					}
				],
				islogin: false,
				showadmin: "",
				showid: "",
				showname: "",
				showclass: "",
				hitokoto: "hitokoto :D 获取中...",
				hitokotoFrom: ""
			}
		},
		onShow() {
			wx.request({
				url: 'https://v1.hitokoto.cn?c=a&c=d&c=i&c=k',
				method: 'GET',
				success: (res) => {
					console.log(res.data);
					this.hitokoto = res.data.hitokoto;
					this.hitokotoFrom = res.data.from;
				}
			})
			let loginState = uni.getStorageSync("loginState");
			if (loginState != "") {
				this.islogin = true;
				this.showid = uni.getStorageSync("showid");
				this.showname = uni.getStorageSync("showname");
				this.showclass = uni.getStorageSync("showclass");
				this.showadmin = uni.getStorageSync("showadmin");
			} else
				this.islogin = false;
		},
		methods: {
			formatTime(date) {
				const year = date.getFullYear();
				const month = String(date.getMonth() + 1).padStart(2, '0');
				const day = String(date.getDate()).padStart(2, '0');
				const hours = String(date.getHours()).padStart(2, '0');
				const minutes = String(date.getMinutes()).padStart(2, '0');
				return `${year}-${month}-${day} ${hours}:${minutes}`;
			},
			showToast_1() {
				uni.showToast({
					title: '你已经打过卡了'
				})
			},
			showToast_2() {
				uni.showToast({
					title: '打卡成功'
				})
			},
			daka() {

				var flag = 1;
				// 获取当前的日期，用来判断今天是不是已经打过卡了
				var now = new Date();
				var format = this.formatTime(now);
				console.log(format);
				// 先从本地获取打卡的数据
				var dakaRecord = uni.getStorageSync('dakaInfo') || [];
				// 和数组中的日期比较，看看是否已经打过卡了(其实之和数组的第一个元素比也行)
				//因为unshift是在最前面插入，所以最前面的肯定是最新的日期
				for (var i = 0; i < dakaRecord.length; i++) {
					if (format.substr(0, 10) == dakaRecord[i].substr(0, 10)) {
						this.showToast_1();
						flag = 0;
						break;
					}
				}
				//第一次打卡
				if (flag) {
					dakaRecord.unshift(format);
					this.showToast_2();
				}
				//将打卡数据传回
				uni.setStorageSync('dakaInfo', dakaRecord);
			}

		}
	}
</script>

<style>
	.subtitle {
		width: 650rpx;
		height: 200rpx;

		margin: 50rpx auto;

		line-height: 80rpx;
		font-size: 40rpx;
		text-align: center;
		
		radius: 10rpx;
	}

	.navigators {
		display: flex;
		justify-content: space-around;

		width: 750rpx;

		flex-wrap: wrap;
	}

	.navigators .navigatorBlock{
		display: flex;
		flex-direction: column;
		margin:0 30rpx
	}

	.navigators .navigatorBlock .imageType{
		display: flex;
		justify-content: center;
		align-items: center;
		
		width: 200rpx;
		height: 200rpx;
	
		margin: 30rpx auto;
		
		line-height: 150rpx;
		text-align: center;
		
		background-position: center;
		background-size: cover;	
	}

	.navigators .navigatorBlock .imageType .navigatorType {
		width: 190rpx;
		height: 190rpx;

		margin: 30rpx auto;
		
		line-height: 150rpx;
		text-align: center;
		
		background-position: center;
		background-size: cover;
	}
	
</style>